import React, { useState } from 'react'
import { useSelector } from 'react-redux'

import PropTypes from 'prop-types'

import './OrdersTable.styles.scss'
import LineBlock from '../../../lib/components/LineBlock'
import RowLineTab from './RowLineTab'
import Button from '../../../lib/components/Button/Button'
/**
 * param type (?id=1232339&type=desadv&mode=add)
 */
const OrdersTable = React.memo(({ type }) => {
  const { tabSumData, ordersTable, dataDesAdvTable } = useSelector(
    (state) => state.desAdv,
  )

  const data = {
    name: 'OrderButton',
    label: 'Создать накладную',
    value: 'regress2506-2',
    type: 'button',
    valueBold: false,
  }

  // let curData = React.useMemo(() => {
  //   return type === 'orders'
  //     ? ordersTable
  //     : type === 'desadv'
  //     ? dataDesAdvTable
  //     : null
  // }, [type, ordersTable])

  if (!ordersTable) {
    return <div>loading..</div>
  }
  return (
    <>
      <div className="tab-block">
        <table className="tcnt-order" cellspacing="4" border="1">
          <thead>
            {Object.keys(ordersTable.header).length && (
              <RowLineTab isHeader values={ordersTable.header} />
            )}
          </thead>
          <tbody>
            {ordersTable.data.length &&
              ordersTable.data.map((values, index) => (
                <RowLineTab
                  isHeader={false}
                  key={values.id['value'] + values.EAN['value']}
                  values={values}
                  odd={index}
                />
              ))}
          </tbody>
        </table>
      </div>

      {type === 'order' ? <Button data={data} /> : null}
      {/* <div className="tab-summ">
        {tabSumData.map((line) => (
          <LineBlock data={line} />
        ))}
      </div> */}
    </>
  )
})

// LineBlock.defaultProps = {
//   name: 'Название(1)',
//   data: {
//     name: 'Номер заказа',
//     value: 'regress2506-2',
//     type: 'div',
//     valueBold: false,
//   },
// }

OrdersTable.propTypes = {
  type: PropTypes.string,
}

OrdersTable.defaultProps = {
  type: 'order',
}

export default OrdersTable
